import {Product} from '../../product-structure/models/product';
import {UITask} from '../components/manufacturing-chart/manufacturing-chart.component';
import {Color, Task} from '@shared/models/task';

export type ListProductProductionGroup = Array<ListProductProduction>;

export class ListProductProduction {
  product: Product;
  products: Product[] = [];
  tasks: UITask[] = [];
  task?: Task;
  isGrouped: boolean;

  /**      */
  isFiltered = true;
  /**      (     isFiltered) */
  isVisible = true;
  /**     */
  isExpanded = true;

  /**    */
  isHover = false;

  /**    */
  isSelected = true;

  /**    /  */
  isActive = false;

  /**   */
  index: number;

  isRootProductionCompleted = false;

  parent: ListProductProduction;
  children: ListProductProduction[] = [];

  color: Color;

  /**   ,        */
  groups: ListProductProductionGroup[] = [];

  reservedFromOtherTaskQuantity?: number;

  constructor(config?: Partial<ListProductProduction>) {
    if (config) {
      Object.assign(this, config);
    }
  }

  get hasChildren(): boolean {
    return Boolean(this.children.length || !this.parent);
  }

  getChildrenEnd() {
    return new Date(Math.max(
      ...this.children
        .map(child => child.tasks)
        .flat()
        .map(task => task.end_date.getTime())
    ));
  }

  get isBlocked(): boolean {
    return this.tasks
      .map(p => p.tasks)
      .flat()
      .some(t => t.status === 'On stock') && !this.isRootProductionCompleted && this.tasks.some(t => !t.created_orders.length);
  }

  getEnd() {
    return new Date(Math.max(
      ...this.tasks
        .map(t => t.tasks)
        .flat()
        .map(task => task.end_date.getTime())
    ));
  }

  public updateGroups() {
    this.groups = [];
    for (const child of this.children) {
      //    2 :  ,  
      const groupIndex = child.product.nomenclature.type === '1' ? 0 : 1;
      this.groups[groupIndex] = this.groups[groupIndex] ?? [] as ListProductProductionGroup;
      this.groups[groupIndex].push(child);
    }
  }
}
